  /*color: #ecf0f1;*/
  color: #4A4A4A;
  font-size:1.5em;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
}

@media screen and (max-width: 1430px) {
    .left-side {
      display: none;
    }
}

